.form{
    width: 100%;
    margin: auto;
    display: flex;
    gap: 5%;
    position: relative;
}
.form svg{
    position: absolute;
    left: 72%;
    top: 50%;
    transform: translate(0, -50%);
    fill: #7c7c7c;
}
.form input{
    width: 75%;
    height: 50px;
    border-radius: 15px;
    padding: 0 15px;
    font-size: 16px;
    background: transparent;
    color: black;
    border: #7c7c7c 1px solid;
}
.form input::placeholder{
    font-size: 16px;
    color: #7c7c7c;
}
.form input:focus-visible{
    outline: 1px rgb(238, 243, 25) solid;
}
.form button{
    height: 50px;
    width: 20%;
    border-radius: 8px;
    background: rgb(252, 255, 122);
    color: black
}
.form button:hover{
    background: #ffff00;
}
.searchResult ul{
    width: 300px;
    background: rgb(245,243,244);
    border-radius: 8px;
    margin-top: 20px;
    padding: 10px;
    position: absolute;
}
.form button a{
    color: black !important;
    text-decoration: none;
}
.searchResult ul li{
    width: 100%;
    padding: 10px 10px;

}
.searchResult ul li a{
    width: 100%;
    color: black;
}
.searchResult ul li:hover{
    background: white;
}
.searchResult ul li a:active{
    color: #232323 !important;
}
.cityList{
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding-top: 150px;
}
.city{
    width: 25%;
    height: 190px;
    background: rgb(245,243,244);
    padding: 0 20px;
}
.city div{
    width: 100%;
    height: 100%;
    background: rgb(252, 255, 122);
    margin-top: -20px;
    padding: 30px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}
.city h3{
    color: #2c2c2c;
    font-size: 25px;
    font-weight: normal;
}
.city p{
    font-size: 18px;
    color: #424242;
    font-weight: bold;
}
.city span{
    color: #2c2c2c;
    font-size: 16px;
    font-weight: normal;
}
.city a{
    color: #232323;
}
