.loadingScreen, .errorScreen{
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
}
.container{
  max-width: 1200px;
  margin: auto;
  padding: 120px 30px;
}
footer{
  width: 100%;
  background: rgb(245,243,244);
  padding: 20px 100px;
}
a{
  text-decoration: none;
}
li{
  list-style: none;
}
h1{
  font-size: 50px;
  text-align: center;
  margin-bottom: 100px;
  font-weight: normal;
  color: #2c2c2c;
}