.weatherImage{
    width: 200px;
    height: auto;
}
.goBack{
    position: absolute;
    top: 0;
    left: 0;
}
.weather{
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}
.weather h2{
    font-size: 30px;
    color: #4f4f4f;
}
.weather p{
    font-size: 20px;
}
.weather div span{
    font-size: 30px;
    display: flex;
    align-items: center;

}
.weather div svg{
    fill: #7c7c7c;
    width: 40px;
    height: 40px;
}
.weather div {
    display: flex;
    margin-top: 30px;
    width: 350px;
    justify-content: space-between;
    position: relative;
}
.weather div i{
    width: 2px;
    height: 50px;
    background: #a8a8a8;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.select{
    margin-top: 100px!important;
    width: 70px!important;
}
.select span{
    font-size: 22px !important;
    cursor: pointer;
}
.select i {
    height: 30px !important;
}